: 50%;\n\tleft: 50%;\n\ttransform: translate(-50%, -50%);\n\tcolor: $main;\n\ttext-shadow: 2px 4px 8px #000000;\n}\n\nmain .hero .hero-title h2 {\n\tfont-size: 42px;\n\tletter-spacing: 1px;\n\ttext-align: center;\n}\n\nmain .hero .hero-title p {\n\ttext-align: center;\n\tfont-size: 18px;\n}\n\nmain .hero img {\n\twidth: 100%;\n\tdisplay: block;\n\theight: 100vh;\n\tobject-fit: cover;\n}\n\nmain .container {\n\tdisplay: grid;\n\tjustify-items: center;\n}\n\nmain .section-title {\n\tpadding: 20px;\n\tfont-size: 26px;\n\tcolor: $title;\n}\n\nmain .container .list {\n\tdisplay: grid;\n\tgap: 15px 15px;\n\twidth: 90%;\n\tjustify-content: center;\n\tmargin-bottom: 20px;\n}\n/* card */\n\nresto-card {\n\tbackground-color: #fdfaf6;\n\tborder-radius: 10px;\n\tbox-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);\n\toverflow: hidden;\n\ttransition: 0.4s;\n\tcursor: pointer;\n}\n\nresto-card:hover {\n\tposition: relative;\n\ttop: 0;\n\tleft: 0;\n\ttransform: scale(0.9, 0.9);\n}\n\nresto-card:focus {\n\tposition: relative;\n\ttop: 0;\n\tleft: 0;\n\ttransform: scale(0.9, 0.9);\n\tborder: 1px solid #557571;\n}\n\n/* card end */\n/* footer start */\nfooter {\n\tdisplay: flex;\n\tflex-direction: column;\n\tjustify-content: center;\n\talign-items: center;\n\tbackground-color: $bg;\n\tbox-shadow: 0px -12px 15px -3px rgba(0, 0, 0, 0.1);\n}\n\nfooter .social-link {\n\tdisplay: flex;\n\tflex-direction: row;\n\tjustify-content: center;\n\talign-items: center;\n}\n\n.button {\n\tpadding: 15px 20px;\n\tfont-size: 20px;\n\ttext-decoration: none;\n\tcolor: $title;\n\tmargin: 0 15px;\n}\n\nfooter .copyright {\n\tmargin: 0 auto;\n\tfont-size: 14px;\n\tfont-weight: 500;\n\tpadding: 10px;\n}\n/* responsive */\n@media screen and (min-width: 750px) {\n\tmain .container .list {\n\t\tdisplay: grid;\n\t\tgrid-template-columns: 1fr 1fr;\n\t}\n}\n@media screen and (min-width: 850px) {\n\t.menu-btn {\n\t\tdisplay: none;\n\t}\n\t.header .brand {\n\t\tmargin: 0;\n\t\tpadding: 5px;\n\t}\n\tnav {\n\t\twidth: auto;\n\t